import React from "react";
import "./App.css";

import { BrowserRouter as Router, Route } from "react-router-dom";

import Topnav from "./components/Topnav/Topnav.js";
import Container from "./components/Container/Container.js";
import Options from "./components/Options/Options.js";
import InvisibleSelect from "./components/InvisibleSelect/index.js";

import Estatutos from "./data/estatutos.js";
import Convenios from "./data/convenios.js";

const SimpleCrypto = require("simple-crypto-js").default;

const Patcher = require("./libs/diff_match_patch_uncompressed.js");
const PatcherInstance = new Patcher.diff_match_patch();
const PatcherInstanceResult = new Patcher.diff_match_patch();

PatcherInstance.Diff_Timeout = 100 * 1000;
PatcherInstanceResult.Diff_Timeout = 100 * 1000;

PatcherInstance.Match_Distance = 10 * 1000 * 1000;
PatcherInstanceResult.Match_Distance = 10 * 1000 * 1000;

PatcherInstance.Match_Threshold = 0.1;
PatcherInstanceResult.Match_Threshold = 0.1;

function diff_lineMode(text1, text2) {
  var dmp = new Patcher.diff_match_patch();
  var a = dmp.diff_linesToWords_(text1, text2);
  var lineText1 = a.chars1;
  var lineText2 = a.chars2;
  var lineArray = a.lineArray;
  var diffs = dmp.diff_main(lineText1, lineText2, false);
  dmp.diff_charsToLines_(diffs, lineArray);
  return diffs;
}

const DEFAULT_SRC = `
Cláusula 1. Esta cláusula está en ambos documentos.

Cláusula 2. Otra cláusula con otro texto diferente. Esta solo está en el master.

Cláusula 3. Esta cláusula está en ambos documentos y los cambios tienen que reflejarse.

Cláusula 4. Match no exacto, pero LOS CAMBIOS también tienen que ser copiados de un documento a otro.
`.trim();

const DEFAULT_SRC_EDITED = `
Cláusula 1. Esta cláusula está en los dos documentos.

Cláusula 2. Otra cláusula con otro texto diferente. Esta solo está en el master.

Cláusula 3. Esta cláusula está en ambos documentos y los cambios tienen que reflejarse.

Cláusula 4. Match no exacto, pero LOS CAMBIOS también tienen que ser copiados de un documento a otro.
`.trim();

const DEFAULT_PATCH = `
Cláusula 1. Esta cláusula está en ambos documentos.

Cláusula 2. Esta cláusula está en ambos documentos y los cambios tienen que reflejarse.

Cláusula 3. Match no exacto, pero LAS DIFERENCIAS también tienen que ser copiados de un documento a otro.
`.trim();

const LOGIN = {
  synx: {
    src: Convenios.trim(),
    patch: Estatutos.trim()
  }
};

function Editor(props) {
  return (
    <React.Fragment>
      {props.title && <div className="kamina-editor-title">{props.title}</div>}
      <textarea
        disabled={props.disabled}
        className="kamina-editor"
        value={props.value}
        onChange={ev => {
          props.onChange(ev.target.value);
        }}
      />
    </React.Fragment>
  );
}

class App extends React.Component {
  state = {
    user: null,
    password: null,

    view: "EDITOR",
    findType: "WORD",
    editorStyle: "AUTO",

    srcText: DEFAULT_SRC,
    srcTextEdited: DEFAULT_SRC_EDITED,
    patchText: DEFAULT_PATCH,
    patchTextEdited: DEFAULT_PATCH,

    srcHtmlDiff: ""
  };

  componentDidMount() {
    this.onChangeKey("srcTextEdited", this.state.srcTextEdited);
  }

  onForceUpdate() {
    this.onChangeKey("srcTextEdited", this.state.srcTextEdited, true);
  }

  onChangeKey(key, val, force) {
    this.setState({
      [key]: val
    });

    this.state.key = val;

    if (key === "srcText") {
      this.setState(state => {
        return {
          srcTextEdited: val,
          patchTextEdited: state.patchText
        };
      });
    }

    if (key === "patchText") {
      this.setState(state => {
        return {
          patchTextEdited: val
        };
      });
    }

    if (key === "srcTextEdited") {
      if (this.state.editorStyle === "MANUAL" && !force) return;

      const text1 = this.state.srcText;
      const text2 = val;
      const textToPatch = this.state.patchText;

      let diff = PatcherInstance.diff_main(text1, text2, true);

      // if (diff.length > 2) {
      //   PatcherInstance.diff_cleanupSemantic(diff);
      // }

      const patch_list = PatcherInstance.patch_make(text1, text2, diff);

      const newText = PatcherInstance.patch_apply(patch_list, textToPatch);

      let diffPatched;
      if (this.state.findType === "WORD") {
        diffPatched = diff_lineMode(textToPatch, newText[0], true);
      } else {
        diffPatched = PatcherInstance.diff_main(textToPatch, newText[0], true);
      }

      function fixHtml(html) {
        return html
          .split("background:#ffe6e6;")
          .join("background:rgba(255,64,0,0.5);")
          .split("background:#e6ffe6;")
          .join("background:rgba(0,255,0,0.5);")
          .replace(/&para;/g, " ");
      }

      this.setState({
        patchTextEdited: newText[0],
        patchHtmlDiff: fixHtml(PatcherInstance.diff_prettyHtml(diffPatched))
      });
    }

    if (key === "findType" || key === "view") {
      this.setState({}, () => {
        this.componentDidMount();
      });
    }
  }

  onLogin() {
    const user = prompt("User", "synx");

    if (!LOGIN[user]) {
      alert("User does not exists.");
      return;
    }

    const password = prompt("Password", "");

    const simpleCrypto = new SimpleCrypto(password);

    let decryptedSrc = null;
    let decryptedPatch = null;
    try {
      decryptedSrc = simpleCrypto.decrypt(LOGIN[user].src);
      decryptedPatch = simpleCrypto.decrypt(LOGIN[user].patch);
      if (decryptedSrc[0] !== "=") {
        decryptedSrc = null;
      }
      if (decryptedPatch[0] !== "=") {
        decryptedPatch = null;
      }
    } catch (e) {}

    if (!decryptedSrc || !decryptedPatch) {
      alert("Incorrect password");
      return;
    }

    this.setState(
      {
        user: user,
        password: password,
        srcText: decryptedSrc,
        srcTextEdited: decryptedSrc,
        patchText: decryptedPatch,
        patchTextEdited: decryptedPatch
      },
      () => {
        this.onChangeKey("srcTextEdited", this.state.srcTextEdited);
      }
    );
  }

  render() {
    const { view, findType, srcText, srcTextEdited, patchText, editorStyle } = this.state;

    return (
      <Router>
        <Topnav>
          <Container className="flex flex-align-center">
            <b style={{ color: "#fff", paddingRight: "1.5rem" }}>REPLICATOR</b>

            <div className="flex flex-1">
              <div style={{ paddingRight: "1rem" }}>
                <div className="kamina-option-label">View</div>
                <Options
                  options={[
                    { name: "Source", value: "SRC" },
                    { name: "Editor", value: "EDITOR" }
                  ]}
                  value={view}
                  onChange={this.onChangeKey.bind(this, "view")}
                />
              </div>

              {false && (
                <div style={{ paddingRight: "1rem" }}>
                  <div className="kamina-option-label">Patch Type</div>
                  <Options
                    options={[
                      { name: "Words", value: "WORD" },
                      { name: "Chars", value: "CHAR" }
                    ]}
                    value={findType}
                    onChange={this.onChangeKey.bind(this, "findType")}
                  />
                </div>
              )}

              {this.state.user && (
                <div style={{ paddingRight: "1rem" }}>
                  <InvisibleSelect
                    options={[
                      { value: "doc1", name: "Doc 1" },
                      { value: "doc2", name: "Doc 2" }
                    ]}
                    placeholder="Select Document"
                    onChange={val => console.log(val)}
                    value={null}>
                    <button>Select Document&#8239; &#8239; &#8239;↓</button>
                  </InvisibleSelect>
                </div>
              )}

              <div style={{ paddingRight: "1rem" }}>
                <div className="kamina-option-label">Editor</div>
                <Options
                  options={[
                    { name: "Auto", value: "AUTO" },
                    { name: "Manual", value: "MANUAL" }
                  ]}
                  value={editorStyle}
                  onChange={this.onChangeKey.bind(this, "editorStyle")}
                />
              </div>

              {editorStyle === "MANUAL" && (
                <button onClick={this.onForceUpdate.bind(this)} className="-alternate">
                  Update
                </button>
              )}

              <div className="flex-1" />

              <div>
                <button onClick={this.onLogin.bind(this)}>Login</button>
              </div>
            </div>
          </Container>
        </Topnav>
        <Route exact path="/replicator">
          {view === "SRC" && (
            <section className="main-content flex">
              <div className="flex-1">
                <Editor
                  title="Master. Original."
                  value={srcText}
                  onChange={this.onChangeKey.bind(this, "srcText")}
                />
              </div>
              <div className="flex-1">
                <Editor
                  title="Derivated. Original."
                  value={patchText}
                  onChange={this.onChangeKey.bind(this, "patchText")}
                />
              </div>
            </section>
          )}

          {view === "EDITOR" && (
            <section className="main-content flex">
              <div className="flex-1">
                <Editor
                  title="Master. Editable."
                  value={srcTextEdited}
                  onChange={this.onChangeKey.bind(this, "srcTextEdited")}
                />
              </div>
              <div className="flex-1">
                <div className="kamina-editor-title">Derivated. Result after patch.</div>
                <div
                  className="kamina-editor -disabled"
                  dangerouslySetInnerHTML={{ __html: this.state.patchHtmlDiff }}
                />
              </div>
            </section>
          )}
        </Route>
      </Router>
    );
  }
}

export default App;
