import React from "react";

function InvisibleSelect(props) {
	const { options = [], onChange = () => {} } = props;

	return (
		<div style={{ position: "relative" }}>
			{props.children}
			<select
				onChange={evt => {
					onChange(evt.target.value);
				}}
				value={props.value}
				style={{
					display: "block",
					position: "absolute",
					top: 0,
					width: "100%",
					left: 0,
					right: 0,
					bottom: 0,
					opacity: 0
				}}>
				<option disabled value={null}>
					{props.placeholder}
				</option>

				{options.map(option => (
					<option value={option.value} key={option.value}>
						{option.name}
					</option>
				))}
			</select>
		</div>
	);
}

export default InvisibleSelect;
